$breakpoint: 768px;

html {
  font-size: unquote('min(0.069vw, 0.11vh)');
  font-family: Roboto;
}

.noto {
  font-family: 'Noto Sans';
}

@media (max-width: $breakpoint) {
  html {
    font-size: 0.267vw;
  }
}

.mainpage {
  width: 100vw;
  position: absolute;
  top: 0;
  overflow-x: hidden;
  overflow-y: auto;
  height: 100vh;
}

.page {
  position: relative;
  height: 100vh;
  width: 100%;
  @media (max-width: $breakpoint) {
    height: auto;
    display: flex;
    flex-direction: column;
    align-items: center;
  }
}

@media (max-width: $breakpoint) {
  .container {
    padding-left: calc(50vw - 35rem);
    padding-right: calc(50vw - 35rem);
  }
}

.container {
  padding-left: calc(50vw - 500rem);
  padding-right: calc(50vw - 500rem);
  width: 100vw;
  position: absolute;
  top: 0;
}
.logoBackground {
  position: sticky;
  top: 0;
  width: 100%;
  height: 10vh;
  margin-bottom: -10vh;
  background-image: linear-gradient(#000000 30%, rgba(0, 0, 0, 0.2));
  z-index: 10;
  @media (max-width: $breakpoint) {
    height: 48rem;
    margin-bottom: -48rem;
  }
}

.logos {
  z-index: 11;
  position: sticky;
  top: 0;
  width: 100%;
  height: 10vh;
  margin-bottom: -10vh;
  display: flex;
  align-items: center;
  justify-content: space-between;
  img {
    height: 16rem;
    width: auto;
  }
  > div {
    display: flex;
    align-items: flex-end;
  }
  .backedby {
    font-size: 16rem;
    color: #ffffff;
    opacity: 0.72;
  }
  @media (max-width: $breakpoint) {
    justify-content: center;
    height: 48rem;
    margin-bottom: -48rem;
    img {
      height: 16rem;
    }
    > div {
      display: none;
    }
  }
}

.scrollMore {
  position: absolute;
  bottom: 2.7vh;
  font-size: 14rem;
  left: 0;
  right: 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  > div {
    opacity: 0.72;
    margin-bottom: 16rem;
  }
  > img {
    height: 40rem;
    width: 40rem;
  }
  @media (max-width: $breakpoint) {
    bottom: 0;
    > img {
      height: 40rem;
      width: 40rem;
    }
  }
}

.wallpaper {
  position: absolute;
  opacity: 0.5;
  top: 0;
  left: 0;
  right: 0;
  height: 100vh;
  z-index: -1;
}
.first.page {
  padding-top: 33vh;
  @media (max-width: $breakpoint) {
    height: 100vh;
  }
}
.second.page {
  padding-top: 100rem;
}
.images {
  > img:nth-child(1) {
    height: 61.1vh;
    width: auto;
    position: absolute;
    top: 20vh;
    left: 580rem;
    border-radius: 8rem;
  }
  > img:nth-child(2) {
    height: 18.6vh;
    width: auto;
    position: absolute;
    top: 65.3vh;
    left: 480rem;
  }
  @media (max-width: $breakpoint) {
    margin-top: 48rem;
    position: relative;
    > img:nth-child(1) {
      position: static;
      width: 304rem;
      border-radius: 4rem;
      height: auto;
    }
    > img:nth-child(2) {
      width: 100rem;
      height: auto;
      top: 134rem;
      left: -30rem;
    }
  }
}
.demoIntro {
  padding-top: 31.3vh;
  @media (max-width: $breakpoint) {
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    padding-top: 0;
  }
  > div:nth-child(1) {
    @media (max-width: $breakpoint) {
      width: 112rem;
      height: 28rem;
      font-size: 12rem;
      margin-bottom: 16rem;
    }
    font-weight: bold;
    letter-spacing: 2rem;
    width: 128rem;
    height: 32rem;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 4rem;
    background-color: rgba(202, 199, 244, 0.19);
    font-size: 14rem;
    color: #acb0d9;
    opacity: 0.8;
    margin-bottom: 24rem;
  }
  > div:nth-child(2) {
    @media (max-width: $breakpoint) {
      font-size: 22rem;
      line-height: 30rem;
      margin-bottom: 16rem;
    }
    letter-spacing: 0.8rem;
    color: #ffffff;
    font-weight: bold;
    font-size: 40rem;
    > span {
      color: #faac14;
    }
    margin-bottom: 24rem;
  }
  > div:nth-child(3) {
    @media (max-width: $breakpoint) {
      font-size: 16rem;
      line-height: 24rem;
      margin-bottom: 48rem;
      ine-height: 1.2;
    }
    letter-spacing: 0.8rem;
    line-height: 1.6;
    color: #ffffff;
    font-size: 20rem;
    margin-bottom: 64rem;
  }
  > div:nth-child(4) {
    color: #faac14;
    font-weight: bold;
    font-size: 20rem;
    line-height: 20rem;
    display: flex;
    align-items: center;
    > a {
      margin-left: 16rem;
    }
    svg {
      color: #faac14;
      height: 40rem;
      width: 40rem;
    }
    @media (max-width: $breakpoint) {
      font-size: 16rem;
      line-height: 16rem;
      > a {
        margin-left: 8rem;
      }
      svg {
        height: 24rem;
        width: 24rem;
      }
    }
  }
}

.titles {
  text-align: center;
  > h1 {
    font-weight: 900;
    font-size: 68rem;
    margin-bottom: 24rem;
    color: white;
    letter-spacing: 2.5rem;
  }
  > h2 {
    font-size: 28rem;
    color: #9e9e9e;
  }
  @media (max-width: $breakpoint) {
    height: auto;
    display: flex;
    flex-direction: column;
    align-items: center;
    > h1 {
      width: 304rem;
      font-size: 32rem;
      line-height: 37.5rem;
      margin-bottom: 8rem;
      color: white;
      word-break: break-word;
      letter-spacing: normal;
      > br {
        display: none;
      }
    }
    > h2 {
      font-size: 18rem;
      line-height: 21rem;
      color: #9e9e9e;
      word-break: break-word;
    }
  }
}

.profile {
  position: relative;
  text-align: center;
  width: 312rem;
  height: 408rem;
  margin-bottom: 72rem;
  > div:nth-child(2) {
    width: 100%;
    font-size: 24rem;
    font-weight: 600;
    height: 40rem;
    color: #ffffff;
    opacity: 0.95;
    @media (max-width: $breakpoint) {
      line-height: 24rem;
      font-size: 20rem;
      height: 24rem;
      margin-bottom: 4rem;
    }
  }
  > div:nth-child(3) {
    width: 100%;
    font-size: 16rem;
    color: #a6a6a6;
    white-space: pre-wrap;
    line-height: 1.5;
    letter-spacing: 0.48rem;
    @media (max-width: $breakpoint) {
      line-height: 16rem;
      font-size: 12rem;
    }
  }
  @media (max-width: $breakpoint) {
    width: 186rem;
    height: 251rem;
  }
}
.profileImage {
  > img {
    width: 312rem;
    height: 312rem;
    background-color: #d1d2d5;
    @media (max-width: $breakpoint) {
      width: 186rem;
      height: 186rem;
    }
  }
  > div {
    @media (max-width: $breakpoint) {
      width: 186rem;
      height: 186rem;
    }
    transition: all 0.1s;
    position: absolute;
    width: 312rem;
    height: 312rem;
    top: 0;
    left: 0;
    opacity: 0;
    font-size: 30rem;
    background-color: rgba(0, 0, 0, 0.6);
    a {
      color: rgba(255, 255, 255, 0.7);
    }
    a + a {
      margin-left: 16rem;
    }
    display: flex;
    align-items: center;
    justify-content: center;
  }
  margin-bottom: 16rem;
}
.profileImage:hover > div {
  opacity: 1 !important;
}

.team {
  position: relative;
  width: 100%;
  padding-top: 14.6vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  @media (max-width: $breakpoint) {
    padding-top: 160rem;
  }
  > div:nth-child(1) {
    width: 168rem;
    height: 32rem;
    letter-spacing: 2rem;
    border-radius: 4rem;
    background-color: rgba(202, 199, 244, 0.19);
    display: flex;
    align-items: center;
    justify-content: center;
    color: rgba(172, 176, 217, 0.8);
    font-size: 14rem;
    font-weight: bold;
    margin-bottom: 24rem;
    @media (max-width: $breakpoint) {
      font-size: 12rem;
      width: 152rem;
      height: 28rem;
      margin-bottom: 24rem;
    }
  }
  > div:nth-child(2) {
    font-size: 40rem;
    font-weight: bold;
    color: #ffffff;
    margin-bottom: 24rem;
    letter-spacing: 0.8rem;
    @media (max-width: $breakpoint) {
      text-align: center;
      margin-bottom: 24rem;
      font-size: 22rem;
    }
  }
  > div:nth-child(3) {
    font-size: 20rem;
    opacity: 0.9;
    color: #ffffff;
    margin-bottom: 72rem;
    line-height: 1.6;
    letter-spacing: 0.4rem;
    @media (max-width: $breakpoint) {
      text-align: center;
      margin-bottom: 48rem;
      font-size: 16rem;
      word-wrap: break-word;
      width: 328rem;
      br {
        display: none;
      }
    }
  }
  > div:nth-child(4) {
    display: flex;
    justify-content: space-between;
    flex-wrap: wrap;
  }
}

.techstars {
  position: relative;
  height: 30.56vw;
  display: flex;
  flex-direction: column;
  justify-content: center;

  > img {
    position: absolute;
    top: 0;
    left: calc(500rem - 50vw);
    width: 100vw;
    height: auto;
    z-index: -1;
  }
  @media (max-width: $breakpoint) {
    display: block;
    height: auto;
    padding-top: 64rem;
    > img {
      left: 0;
      width: auto;
      height: 480rem;
    }
  }
}
.techstars > div {
  display: flex;
  justify-content: space-between;
  @media (max-width: $breakpoint) {
    display: block;
  }
}

.techstarsLeftCol {
  @media (max-width: $breakpoint) {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  > div:nth-child(1) {
    width: 136rem;
    height: 32rem;
    letter-spacing: 2rem;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 4rem;
    background-color: rgba(255, 255, 255, 0.16);
    font-size: 14rem;
    font-weight: bold;
    color: rgba(255, 255, 255, 0.8);
    margin-bottom: 24rem;
    @media (max-width: $breakpoint) {
      width: 136rem;
      height: 28rem;
      font-size: 12rem;
    }
  }
  > div:nth-child(2) {
    line-height: 1.4;
    letter-spacing: 0.8rem;
    font-size: 40rem;
    font-weight: bold;
    color: #ffffff;
    line-height: 1.4;
    > span {
      color: #5bf1a0;
    }
    margin-bottom: 64rem;
    @media (max-width: $breakpoint) {
      text-align: center;
      font-size: 22rem;
      margin-bottom: 48rem;
    }
  }
  > div:nth-child(3) {
    display: flex;
    align-items: center;
    color: #5bf1a0;
    font-size: 20rem;
    font-weight: bold;
    letter-spacing: 0.4rem;
    svg {
      color: #5bf1a0;
      width: 40rem;
      height: 40rem;
      margin-left: 16rem;
    }
    @media (max-width: $breakpoint) {
      svg {
        font-size: 16rem;
        width: 24rem;
        height: 24rem;
        margin-left: 8rem;
      }
    }
  }
}

.circleIndicator {
  width: 96rem;
  height: 96rem;
  padding-top: 22rem;
  border: solid 1rem rgba(255, 255, 255, 0.8);
  border-radius: 48rem;
  @media (max-width: $breakpoint) {
    font-size: 16rem;
    width: 70rem;
    height: 70rem;
    border-radius: 35rem;
    padding-top: 15rem;
  }
  > div {
    width: 100%;
    text-align: center;
  }
  > div:nth-child(1) {
    font-size: 28rem;
    font-weight: 900;
    color: #ffffff;
    @media (max-width: $breakpoint) {
      font-size: 22rem;
    }
  }
  > div:nth-child(2) {
    font-size: 16rem;
    font-weight: 500;
    color: rgba(255, 255, 255, 0.64);
    @media (max-width: $breakpoint) {
      font-size: 12rem;
    }
  }
}
.clock {
  display: flex;
  > div + div {
    margin-left: 8rem;
  }
}

.techstarsRightCol {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 60rem;
  > div:nth-child(1) {
    font-size: 14rem;
    font-weight: 900;
    color: #ebebeb;
    letter-spacing: 0.28rem;
    margin-bottom: 16rem;
    @media (max-width: $breakpoint) {
      font-size: 10rem;
    }
  }
  @media (max-width: $breakpoint) {
    margin-top: 50rem;
  }
}

.contectUs {
  margin-top: 40rem;
  display: flex;
  align-items: center;
  width: 100%;
  justify-content: space-between;
  margin-bottom: 40rem;
  @media (max-width: $breakpoint) {
    margin-top: 128rem;
    flex-direction: column;
    justify-content: normal;
  }
  > div {
    font-size: 24rem;
    font-weight: bold;
    color: #ffffff;
    @media (max-width: $breakpoint) {
      font-size: 22rem;
      margin-bottom: 48rem;
    }
  }
  > div:nth-child(2) {
    display: flex;
    align-items: center;
    font-size: 20rem;
    a {
      margin-left: 16rem;
    }
    svg {
      height: 40rem;
      width: 40rem;
      color: #ffffff;
    }
    @media (max-width: $breakpoint) {
      font-size: 16rem;
      a {
        margin-left: 8rem;
      }
      svg {
        height: 24rem;
        width: 24rem;
      }
    }
  }
}

footer {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  color: #ffffff;
  opacity: 0.9;
  font-size: 14rem;
  letter-spacing: 0.48rem;
  > div + div {
    margin-left: 24rem;
  }
  margin-top: 32rem;
  margin-bottom: 32rem;
  @media (max-width: $breakpoint) {
    margin-top: 0;
    margin-bottom: 0;
    flex-direction: column;
    height: 84rem;
    justify-content: center;
    font-size: 12rem;
    > div + div {
      margin-left: 0;
      margin-top: 8rem;
    }
  }
}
.footerHr {
  border-top: 2rem solid #3f00ff;
  border-bottom: none;
  position: relative;
  left: -50vw;
  width: 200vw;
}
